<template>
  <div style="background-color: #E0E0E0;">
    <!-- <v-container
      fluid
      grid-list-md
      color="grey darken-3"
    > -->
    <v-tabs fixed-tabs>
      <v-tab
        v-for="t in tabs"
        :key="t.name"
        @click="queryClassified(t.code)"
      >
        {{ t.name }}
      </v-tab>
    </v-tabs>
    <v-card flat>
      <v-container
        fluid
        grid-list-lg
        style="background-color: #E0E0E0;"
      >

        <OrderPreview
          v-for="order in orders"
          :key="order.orderNo"
          :order="order"
          @view-details="viewDetails($event)"
        />
      </v-container>
      到头了
    </v-card>
    <v-footer app>
      共{{ page.total }}条
      <v-pagination
        v-model="page.page"
        :length="Math.ceil(page.total/page.limit )"
        :total-visible="5"
        @input="getOrderList();$vuetify.goTo(0);"
      ></v-pagination>
    </v-footer>
    <!-- </v-container> -->
    <ViewOrder
      :show="viewOrder.show"
      :order-no="viewOrder?viewOrder.orderNo:null"
      @view-order-close="viewOrder.show = false"
    />

    <v-btn
      color="primary"
      dark
      small
      fab
      fixed
      top
      right
      style="top: 100px"
      @click.stop="searchShow = true"
    >
      <v-icon>search</v-icon>
    </v-btn>

    <!-- loaging 弹框 -->
    <v-dialog
      v-model="loading"
      width="50"
    >
      <v-card
        color="primary"
        dark
        class="text-xs-center"
      >
        <v-progress-circular
          indeterminate
          color="red"
        ></v-progress-circular>
      </v-card>

    </v-dialog>
    <!-- 查询弹窗 -->
    <v-dialog v-model="searchShow" fullscreen > 
      <v-card>
        <v-toolbar
          dark
          color="primary"
        >
          <v-btn
            icon
            dark
            @click="searchShow = false"
          >
            <v-icon>close</v-icon>
          </v-btn>
          <v-toolbar-title>查询</v-toolbar-title>
          <v-spacer></v-spacer>
          <v-toolbar-items>
          </v-toolbar-items>
        </v-toolbar>

        <v-dialog
          ref="dateStrat"
          v-model="dateStratShow"
          :return-value.sync="query.startTime"
          persistent
          lazy
          full-width
          width="290px"
        >
          <template v-slot:activator="{ on }">
            <v-text-field
              v-model="query.startTime"
              clearable
              label="营业日-开始"
              prepend-icon="event"
              readonly
              v-on="on"
              width="150px"
            ></v-text-field>
          </template>
          <v-date-picker
            v-model="query.startTime"
            no-title
            scrollable
            locale="zh-cn"
          >
            <v-spacer></v-spacer>
            <v-btn
              flat
              color="primary"
              @click="dateStratShow = false"
            >取消</v-btn>
            <v-btn
              flat
              color="primary"
              @click="$refs.dateStrat.save(query.startTime)"
            >确定</v-btn>
          </v-date-picker>
        </v-dialog>

         <v-dialog
          ref="dateEnd"
          v-model="dateEndShow"
          :return-value.sync="query.endTime"
          persistent
          lazy
          full-width
          width="290px"
        >
          <template v-slot:activator="{ on }">
            <v-text-field
              v-model="query.endTime"
              clearable
              label="营业日-结束(不包括)"
              prepend-icon="event"
              readonly
              v-on="on"
            ></v-text-field>
          </template>
          <v-date-picker
            v-model="query.endTime"
            no-title
            scrollable
            locale="zh-cn"
          >
            <v-spacer></v-spacer>
            <v-btn
              flat
              color="primary"
              @click="dateEndShow = false"
            >取消</v-btn>
            <v-btn
              flat
              color="primary"
              @click="$refs.dateEnd.save(query.endTime)"
            >确定</v-btn>
          </v-date-picker>
        </v-dialog>

        <v-text-field
        prepend-icon="man"
        clearable
        name="guestName"
        label="客人姓名"
        type="text"
        v-model="query.guestName"
      >
      </v-text-field>

        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn
            flat
            @click="searchShow = false"
          >取消</v-btn>
          <v-btn
            flat
            @click="restQuery"
          >重置</v-btn>
          <v-btn
              dark
              color="primary"
              @click="getOrderList();searchShow = false"
            >查询</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
import OrderPreview from "../components/order/OrderPreview.vue";
import { getOrders } from "@/api/order.js";
import queryUtils from "@/util/queryUtils.js";
import ViewOrder from "../components/order/ViewOrder.vue";
import { mapGetters, mapState, mapActions } from "vuex";
import { setTimeout } from "timers";
export default {
  name: "Order",
  components: {
    OrderPreview,
    ViewOrder
  },
  data() {
    return {
      tabs: [
        {
          name: "全部",
          code: "all"
        },
        {
          name: "正住",
          code: "live"
        },
        {
          name: "未到",
          code: "no_come"
        },
        // {
        //   name: "异常",
        //   code: "ex"
        // }
      ],
      orders: [],
      query: {
        orderNo: null,
        startTime: "",
        endTime: "",
        ss: null,
        guestName: null,
        "orderRoomQuery.orderRoomStateCode": null,
        "orderRoomQuery.estimateOutTimeStart": null,
        "orderRoomQuery.estimateOutTimeEnd": null,
        "orderRoomQuery.estimateInTimeStart": null,
        "orderRoomQuery.estimateInTimeEnd": null,
        queryType: null,
        page: 1,
        limit: 15,
        sortBy: "gmtCreate",
        orderBy: "desc"
      },
      page: {
        total: 0,
        page: 1,
        limit: 15,
        pageSizeOpts: [5, 10, 15, 20, 30, 40]
      },
      viewOrder: {
        show: false,
        orderNo: null
      },
      isShowDate: true,
      businessDayEndHour: 6,
      loading: false,
      searchShow: false,
      dateStratShow: false,
      dateEndShow: false
    };
  },
  computed: {
    ...mapState({
      channel: state => state.dictionary.channel,
      pay_type: state => state.dictionary.pay_type,
      order_ctate_code: state => state.dictionary.order_ctate_code
    })
  },
  mounted() {
    this.getOrderList();

    if (!this.channel) {
      this.getDicByType("channel");
    }
    if (!this.pay_type) {
      this.getDicByType("pay_type");
    }
    if (!this.order_ctate_code) {
      this.getDicByType("order_ctate_code");
    }
  },
  methods: {
    getOrderList() {
      this.loading = true;

      this.query.page = this.page.page;
      this.query.limit = this.page.limit;

      this.query.startTime = queryUtils.getBusinessDate(
        this.query.startTime,
        this.businessDayEndHour
      );
      this.query.endTime = queryUtils.getBusinessDate(
        this.query.endTime,
        this.businessDayEndHour
      );
      getOrders(this.query).then(res => {
        if (res.data.code === 0) {
          this.orders = res.data.data;
          this.page.total = res.data.count;
        } else {
          this.$Msg.error(res.data.code + "：" + res.data.msg);
        }
        let vm = this;
        setTimeout(function() {
          vm.loading = false;
        }, 500);
      });
    },
    viewDetails(orderNo) {
      this.viewOrder.orderNo = orderNo;
      this.viewOrder.show = true;
    },
    restQuery() {
      this.query.startTime = "";
      this.query.endTime = "";
      this.query.guestName=null;
    },
    ...mapActions({
      getDicByType: "getDicByType"
    }),
    queryClassified(val) {
      Object.assign(this.$data.query, this.$options.data().query);
      Object.assign(this.$data.page, this.$options.data().page);
      this.orders = [];
      if ("all" === val) {
        this.isShowDate = true;
      }
      if ("live" === val) {
        this.isShowDate = false;
        this.query["orderRoomQuery.orderRoomStateCode"] = "live";
        this.query.queryType = "live";
      }
      if ("out" === val) {
        this.isShowDate = false;
        this.query["orderRoomQuery.orderRoomStateCode"] = "live";
        this.query[
          "orderRoomQuery.estimateOutTimeStart"
        ] = queryUtils.getBusinessDate(new Date(), this.businessDayEndHour);
        this.query[
          "orderRoomQuery.estimateOutTimeEnd"
        ] = queryUtils.getBusinessDate(
          new Date().setDate(new Date().getDate() + 1),
          this.businessDayEndHour
        );
        this.query.queryType = "live";
      }

      if ("wait_allot" === val) {
        this.isShowDate = false;
        this.query.queryType = "reserve";
      }

      if ("no_come" === val) {
        this.isShowDate = false;
        this.query["orderRoomQuery.orderRoomStateCode"] = "no_live";
        this.query.queryType = "reserve";
      }

      if ("today_come" === val) {
        this.isShowDate = false;
        this.query["orderRoomQuery.orderRoomStateCode"] = "no_live";
        this.query[
          "orderRoomQuery.estimateInTimeStart"
        ] = queryUtils.getBusinessDate(new Date(), this.businessDayEndHour);
        this.query[
          "orderRoomQuery.estimateInTimeEnd"
        ] = queryUtils.getBusinessDate(
          new Date().setDate(new Date().getDate() + 1),
          this.businessDayEndHour
        );
        this.query.queryType = "reserve";
      }

      this.getOrderList();
    }
  }
};
</script>

<style>
.s {
  background-color: grey lighten-3;
  float: right;
}
</style>
