<template>
  <v-card
    class="mt-2"
    style="max-width: 400px;"
  >

    <v-card-title>
      <v-layout>
        <v-flex 7>
          <div style="float: left;">{{order.orderNo}}</div>
        </v-flex>
        <v-flex 5>
          <div style="float: right;">{{order?getText('order_ctate_code', order.orderStateCode):''}}</div>
        </v-flex>
      </v-layout>
      <div>
        <div >
            <span class="headline">{{order.guestName}}</span>
            {{ order?getText('channel', order.channelCode):'' }} 
                        <v-chip  color="green" small class="overline">
                          {{ order?getText('pay_type', order.payTypeCode):'' }}
                        </v-chip> 
<!-- {{ order.orderRooms[0].roomName}} -->
        </div>
        <div class="subtitle-2">
          创建时间：{{ order?order.gmtCreate:'' }}
        </div>
        <div>已消费：{{ order?order.orderMoney:'' }} 元 
            已收款：{{ order?order.payMoney:'' }} 元
        </div>
      </div>
      <div>
          <div v-if="showReserve" v-for="(item, index) in order&& order.orderReserves?order.orderReserves:[]" :key="index">
              预订 {{ item.roomTypeName}} {{ item.reserveNum}} 间 已分配 {{ item.allottedNum}} 间
          </div>
          <div v-if="order && order.orderRooms && order.orderRooms.length > 0" 
              v-for="(item, index) in order&& order.orderRooms?order.orderRooms:[]" :key="'r'+index">
              {{ item.roomName}}（{{ item.roomTypeName}}）  {{ item.orderRoomStateCode | orderRoomState }}
          </div>
      </div>
    </v-card-title>
    <v-card-actions>
      <v-spacer></v-spacer>
      <v-btn
        flat
        @click="viewDetails"
      >查看详情</v-btn>
    </v-card-actions>
  </v-card>
</template>

<script>
import { mapGetters } from "vuex";
export default {
  name: "OrderPreview",
   filters: {
    orderRoomState(code) {
      switch (code) {
        case "live":
          return "正住";
        case "left":
          return "已退";
        case "no_live":
          return "未入住";
        default:
          return "";
      }
    },
  },
  props: {
    order: Object
  },
  data() {
    return {};
  },
  computed: {
      showReserve() {
      let show = false;
      if ( this.order &&
        this.order.orderReserves &&
        this.order.orderReserves.length > 0
      ) {
        this.order.orderReserves.forEach(element => {
          if (element.allottedNum < element.reserveNum) {
            show = true;
          }
        });
      }
      return show;
    },
      ...mapGetters([
      'getText'
    ])
  },
  mounted() {
      
  },
  methods: {
    viewDetails() {
      this.$emit("view-details", this.order.orderNo);
    },
    
  }
};
</script>

